// =============================================================================
// GET /api/metrics/requests - Request Duration Metrics
// =============================================================================
// Returns request latency statistics per endpoint

import { NextRequest, NextResponse } from 'next/server';
import { getRequestMetrics, getEndpointMetrics } from '@/lib/request-metrics';
import { requireAuth } from '@/lib/auth';

export async function GET(request: NextRequest) {
  // Require auth to view metrics
  const auth = await requireAuth(request, ['admin', 'user']);
  if (!auth.authorized) {
    return NextResponse.json(
      { error: 'Unauthorized', code: 'UNAUTHORIZED' },
      { status: 401 }
    );
  }
  
  // Check for specific endpoint filter
  const endpoint = request.nextUrl.searchParams.get('endpoint');
  
  if (endpoint) {
    const metrics = getEndpointMetrics(endpoint);
    if (!metrics) {
      return NextResponse.json(
        { error: 'Endpoint not found', code: 'NOT_FOUND' },
        { status: 404 }
      );
    }
    return NextResponse.json({
      success: true,
      endpoint,
      data: metrics,
      timestamp: new Date().toISOString(),
    });
  }
  
  // Return all metrics
  const metrics = getRequestMetrics();
  
  return NextResponse.json({
    success: true,
    data: metrics,
    timestamp: new Date().toISOString(),
  });
}

