// =============================================================================
// REQUEST METRICS
// =============================================================================
// Track request duration and latency percentiles per endpoint.
// Used by API routes to measure performance.

// =============================================================================
// TYPES
// =============================================================================

interface EndpointMetrics {
  latencies: number[];
  count: number;
  errors: number;
  lastUpdated: number;
}

interface PercentileStats {
  p50: number;
  p95: number;
  p99: number;
  avg: number;
  min: number;
  max: number;
}

// =============================================================================
// STATE
// =============================================================================

const metrics = new Map<string, EndpointMetrics>();
const MAX_SAMPLES = 1000;
const CLEANUP_INTERVAL_MS = 60 * 60 * 1000; // 1 hour

let lastCleanup = Date.now();

// =============================================================================
// INTERNAL
// =============================================================================

function cleanupOldMetrics(): void {
  const now = Date.now();
  if (now - lastCleanup < CLEANUP_INTERVAL_MS) return;
  
  // Remove endpoints not updated in the last hour
  const staleThreshold = now - CLEANUP_INTERVAL_MS;
  metrics.forEach((value, key) => {
    if (value.lastUpdated < staleThreshold) {
      metrics.delete(key);
    }
  });
  
  lastCleanup = now;
}

function calculatePercentile(sorted: number[], p: number): number {
  if (sorted.length === 0) return 0;
  const idx = Math.ceil(sorted.length * p) - 1;
  return sorted[Math.max(0, Math.min(idx, sorted.length - 1))];
}

function getPercentileStats(latencies: number[]): PercentileStats {
  if (latencies.length === 0) {
    return { p50: 0, p95: 0, p99: 0, avg: 0, min: 0, max: 0 };
  }
  
  const sorted = [...latencies].sort((a, b) => a - b);
  const sum = sorted.reduce((a, b) => a + b, 0);
  
  return {
    p50: Math.round(calculatePercentile(sorted, 0.5)),
    p95: Math.round(calculatePercentile(sorted, 0.95)),
    p99: Math.round(calculatePercentile(sorted, 0.99)),
    avg: Math.round(sum / sorted.length),
    min: sorted[0],
    max: sorted[sorted.length - 1],
  };
}

// =============================================================================
// PUBLIC API
// =============================================================================

/**
 * Record a request duration for an endpoint
 */
export function recordRequestDuration(
  endpoint: string,
  durationMs: number,
  isError: boolean = false
): void {
  cleanupOldMetrics();
  
  let endpointMetrics = metrics.get(endpoint);
  if (!endpointMetrics) {
    endpointMetrics = {
      latencies: [],
      count: 0,
      errors: 0,
      lastUpdated: Date.now(),
    };
    metrics.set(endpoint, endpointMetrics);
  }
  
  // Add latency sample (keep last N)
  endpointMetrics.latencies.push(durationMs);
  if (endpointMetrics.latencies.length > MAX_SAMPLES) {
    endpointMetrics.latencies.shift();
  }
  
  endpointMetrics.count++;
  if (isError) {
    endpointMetrics.errors++;
  }
  endpointMetrics.lastUpdated = Date.now();
}

/**
 * Create a timer for measuring request duration
 */
export function createRequestTimer(endpoint: string): {
  end: (isError?: boolean) => number;
  elapsed: () => number;
} {
  const start = Date.now();
  
  return {
    end: (isError = false) => {
      const duration = Date.now() - start;
      recordRequestDuration(endpoint, duration, isError);
      return duration;
    },
    elapsed: () => Date.now() - start,
  };
}

/**
 * Higher-order function to wrap async handlers with timing
 */
export function withRequestTiming<T extends (...args: any[]) => Promise<any>>(
  endpoint: string,
  handler: T
): T {
  return (async (...args: Parameters<T>): Promise<ReturnType<T>> => {
    const timer = createRequestTimer(endpoint);
    try {
      const result = await handler(...args);
      timer.end(false);
      return result;
    } catch (error) {
      timer.end(true);
      throw error;
    }
  }) as T;
}

/**
 * Get metrics for all endpoints
 */
export function getRequestMetrics(): {
  endpoints: Record<string, {
    count: number;
    errors: number;
    errorRate: string;
    latency: PercentileStats;
  }>;
  totals: {
    requests: number;
    errors: number;
    errorRate: string;
  };
} {
  const endpoints: Record<string, any> = {};
  let totalRequests = 0;
  let totalErrors = 0;
  
  metrics.forEach((value, key) => {
    totalRequests += value.count;
    totalErrors += value.errors;
    
    endpoints[key] = {
      count: value.count,
      errors: value.errors,
      errorRate: value.count > 0 
        ? ((value.errors / value.count) * 100).toFixed(2) + '%'
        : '0%',
      latency: getPercentileStats(value.latencies),
    };
  });
  
  return {
    endpoints,
    totals: {
      requests: totalRequests,
      errors: totalErrors,
      errorRate: totalRequests > 0
        ? ((totalErrors / totalRequests) * 100).toFixed(2) + '%'
        : '0%',
    },
  };
}

/**
 * Get metrics for a specific endpoint
 */
export function getEndpointMetrics(endpoint: string): {
  count: number;
  errors: number;
  errorRate: string;
  latency: PercentileStats;
} | null {
  const endpointMetrics = metrics.get(endpoint);
  if (!endpointMetrics) return null;
  
  return {
    count: endpointMetrics.count,
    errors: endpointMetrics.errors,
    errorRate: endpointMetrics.count > 0
      ? ((endpointMetrics.errors / endpointMetrics.count) * 100).toFixed(2) + '%'
      : '0%',
    latency: getPercentileStats(endpointMetrics.latencies),
  };
}

/**
 * Reset all metrics (for testing)
 */
export function resetRequestMetrics(): void {
  metrics.clear();
  lastCleanup = Date.now();
}

